.rainbow {
  width: 100vw;
  height: 100vh;
}

.rainbow div {
  height: calc(100vh / 7);
  width: 100%;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: center;
      justify-content: center;
}

.rainbow div p {
  display: none;
  font-size: 24px;
}

.rainbow .red {
  background: red;
}

.rainbow .orange {
  background: orange;
}

.rainbow .yellow {
  background: yellow;
}

.rainbow .green {
  background: green;
}

.rainbow .blue {
  background: blue;
}

.rainbow .indigo {
  background: indigo;
}

.rainbow .violet {
  background: violet;
}

.all-colors {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
}

.all-colors div {
  width: 20%;
  padding-top: 20%;
  position: relative;
  overflow: hidden;
  outline: none;
  background: grey;
}

.all-colors div p {
  text-align: right;
  background: #00000040;
  padding: 5px;
  position: absolute;
  width: 100%;
  bottom: -16px;
  right: 0;
  color: white;
}

.all-colors div > p {
  -webkit-transition: .4s linear;
  -o-transition: .4s linear;
  transition: .4s linear;
}

.all-colors div > p.hidden {
  opacity: 0;
  -webkit-transition: none;
  -o-transition: none;
  transition: none;
}

.all-colors div:hover > p.color, .all-colors div:active > p.color, .all-colors div:focus > p.color  {
  background: transparent;
  color: transparent;
}

.all-colors div:hover > p.hidden, .all-colors div:active > p.hidden, .all-colors div:focus > p.hidden{
  opacity: 1;
  height: 100%;
  bottom: 0;
  top: 0;
  margin: 0;
  text-align: left;
  left: 0;
  padding: 10px;
}

.all-colors div.add-color {
  background: -webkit-gradient(linear, left top, right top, from(#f70808) , to(#00ebff));
  background: -webkit-linear-gradient(left, #f70808 , #00ebff);
  background: -o-linear-gradient(left, #f70808 , #00ebff);
  background: linear-gradient(to right, #f70808 , #00ebff);
  cursor: pointer;
  outline: none;
  -webkit-transition: 1s linear;
  -o-transition: 1s linear;
  transition: 1s linear;
}

.all-colors div.add-color:hover, .all-colors div.add-color:active, .all-colors div.add-color:focus {
  background: -webkit-gradient(linear, left top, right top, from(#00ebff) , to(#f70808));
  background: -webkit-linear-gradient(left, #00ebff , #f70808);
  background: -o-linear-gradient(left, #00ebff , #f70808);
  background: linear-gradient(to right, #00ebff , #f70808);
}

.all-colors .add-color p{
  color: white;
  font-size: 26px;
  text-align: center;
  margin: auto;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  position: absolute;
  padding: 0;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  width: 100%;
  -ms-flex-pack: center;
      justify-content: center;
  text-transform: uppercase;
}

@media(max-width: 920px) {
  .all-colors div {
    width: 25%;
    padding-top: 25%;
  }
}

@media(max-width: 520px) {
  .all-colors div{
    width: 50%;
    padding-top: 50%;
  }
}


